.bicycles-types-list {
  display: flex;
  justify-content: space-between;
  width:1126px;
  margin:51px 0 0 0;
  padding:0;

  @media (max-width: $desktop-medium-width) {
    width:900px;
  }
}
.bicycles-types-list__element {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width:342px;
  border-radius: 10px;

  @media (max-width: $desktop-medium-width) {
    width:290px;

    img {
      width:280px;
    }
  }

  p {
    font-weight: 600;
    font-size: 22px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.5px;
    color: #000000;
    margin:20px 0 0 0;
  }
}

.bicycles-types-list__element--road {
  height:559px;
  z-index: 1;

  &:before {
    content:"";
    position: absolute;
    top:156px;
    left:-60px;
    width:180px;
    height:180px;
    border: 27px solid $main;
    box-sizing: border-box;
    border-radius: 50%;
    z-index: -1;
  }

  @media (max-width: $desktop-medium-width) {
    height:469px;

    img {
     height:412px;
    }

    &:before {
      top:111px;
      width:180px;
      height:180px;
    }
  }
}
.bicycles-types-list__element--mount {
  margin:39px;
  height:622px;

  &:before {
    content:"";
    position: absolute;
    top:-18px;
    left:-19px;
    width:47px;
    height:47px;
    border: 8px solid #0AD9C6;
    box-sizing: border-box;
  }

  &:after {
    content:"";
    position: absolute;
    bottom:32px;
    right:-24px;
    width:47px;
    height:47px;
    border: 8px solid #0AD9C6;
    box-sizing: border-box;
  }

  @media (max-width: $desktop-medium-width) {
    height:520px;

    img {
     height:464px;
    }
  }
}
.bicycles-types-list__element--child {
  height:569px;

  &:before {
    content:"";
    position: absolute;
    top:-32px;
    right:-36px;
    width:95px;
    height:95px;
    border: 17px solid #0AD9C6;
    box-sizing: border-box;
    border-radius: 50%;
  }

  @media (max-width: $desktop-medium-width) {
    height:477px;

    img {
     height:420px;
    }
  }
}
