.bicycles-types {
  display:flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height:1038px;
  background-color:#F6F7F9;

  @media (max-width: $desktop-medium-width) {
    height:940px;
  }
}

.bicycles-types__name {
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  color: $dark;
  margin:149px 0 0 0;
}
